/*
Orginal Style from ethanschoonover.com/solarized (c) Jeremy Hull <sourdrums@gmail.com>
*/
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
}
.hljs,
.docs-example-source-copy {
  background: #fdf6e3;
  color: #657b83;
}
.hljs-shadow {
  position: absolute;
  bottom: 0;
  margin: 0;
  left: 0;
  height: 44px;
  width: 100%;
  opacity: 1;
  background: linear-gradient(0deg, #fdf6e3 0%, rgba(253, 246, 227, 0.5) 50%, rgba(253, 246, 227, 0) 100%);
}
.hljs-shadow_hidden {
  opacity: 0;
}
.hljs-line-numbers {
  background-color: #EDE7D6;
  color: #B2B7B3;
}
.hljs-comment,
.hljs-quote {
  color: #93a1a1;
}
/* Solarized Green */
.hljs-keyword,
.hljs-selector-tag,
.hljs-addition {
  color: #859900;
}
/* Solarized Cyan */
.hljs-number,
.hljs-string,
.hljs-meta .hljs-meta-string,
.hljs-literal,
.hljs-doctag,
.hljs-regexp {
  color: #2aa198;
}
/* Solarized Blue */
.hljs-title,
.hljs-section,
.hljs-name,
.hljs-selector-id,
.hljs-selector-class {
  color: #268bd2;
}
/* Solarized Yellow */
.hljs-attribute,
.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-class .hljs-title,
.hljs-type {
  color: #b58900;
}
/* Solarized Orange */
.hljs-symbol,
.hljs-bullet,
.hljs-subst,
.hljs-meta,
.hljs-meta .hljs-keyword,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-link {
  color: #cb4b16;
}
/* Solarized Red */
.hljs-built_in,
.hljs-deletion {
  color: #dc322f;
}
.hljs-formula {
  background: #eee8d5;
}
.hljs-emphasis {
  font-style: italic;
}
.hljs-strong {
  font-weight: bold;
}
